<template>
    <div class="form__counter">
        <button type="button" aria-label="  " v-on:click="currentAmount--">
            <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="text" name="count" v-model.number="currentAmount" v-on:input="inputNumbers" v-on:paste.prevent>

        <button type="button" aria-label="  " v-on:click="currentAmount++">
            <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>

<script>
import inputNumbers from '@/helpers/inputNumbers';
export default {
    props: [
        'amount'
    ],
    data() {
        return {
            currentAmount: 1
        }
    },
    watch: {
        currentAmount(value) {
            if (value < 1) {
                value = 1;
                this.currentAmount = value;
            }
            this.count();
        }
    },
    methods: {
        count() {
            this.$emit('update:amount', this.currentAmount);
        },
        inputNumbers
    }
}
</script>