<template>
    <ul class="catalog__pagination pagination">
        <li class="pagination__item">
            <a href="#" class="pagination__link pagination__link--arrow" aria-label=" "
                v-bind:class="{ 'pagination__link--disabled': page === 1 }" v-on:click.prevent="paginate(page - 1)">
                <svg width="8" height="14" fill="currentColor">
                    <use xlink:href="#icon-arrow-left"></use>
                </svg>
            </a>
        </li>
        <li class="pagination__item" v-for="pageNum in pages">
            <a href="#" class="pagination__link" v-bind:class="{ 'pagination__link--current': pageNum === page }"
                v-bind:key="pageNum" v-on:click.prevent="paginate(pageNum)">
                {{ pageNum }}
            </a>
        </li>
        <li class="pagination__item">
            <a href="#" class="pagination__link pagination__link--arrow" aria-label=" "
                v-bind:class="{ 'pagination__link--disabled': page == pages }" v-on:click.prevent="paginate(page + 1)">
                <svg width="8" height="14" fill="currentColor">
                    <use xlink:href="#icon-arrow-right"></use>
                </svg>
            </a>
        </li>
    </ul>
</template>
<script>
export default {
    model: {
        prop: 'page',
        event: 'paginate'
    },
    props: [
        'page',
        'items',
        'perPage'
    ],
    computed: {
        pages() {
            return Math.ceil(this.items / this.perPage);
        },
    },
    methods: {
        paginate(page) {
            page > 0 && page <= this.pages && this.$emit('paginate', page);
        }
    }
};
</script>
