<template>
    <main class="content container">
        <div class="content__top content__top--catalog">
            <h1 class="content__title">
                
            </h1>
            <span class="content__info">
                : {{ products.length || 0 }}
            </span>
        </div>

        <div class="content__catalog">
            <ProductFilter v-bind:price-from.sync="filterPriceFrom" v-bind:price-to.sync="filterPriceTo"
                v-bind:category-id.sync="filterCategoryId" v-bind:products-props.sync="filterProductsProps"
                v-bind:colors-id.sync="filterColorsId" v-bind:filtering.sync="filtering" />
            <Preloader v-if="productsLoading" />
            <Fail v-else-if="productsLoadingFailed" v-bind:callback="loadProducts">    ...</Fail>
            <section class="catalog" v-else>
                <ProductList v-bind:products="products" />
                <BasePagination v-model:page="page" v-bind:items="items" v-bind:perPage="productsPerPage" />
            </section>
        </div>
    </main>
</template>
<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import containsSome from '@/helpers/containsSome';

export default {
    components: {
        ProductList,
        BasePagination,
        ProductFilter
    },
    data() {
        return {
            productsData: null,
            page: 1,
            productsPerPage: 12,
            filterPriceFrom: '',
            filterPriceTo: '',
            filterCategoryId: 0,
            filterColorsId: [],
            filterProductsProps: null,
            productsLoading: false,
            productsLoadingFailed: false,
            filtering: false
        };
    },
    computed: {
        products() {
            return this.productsData ? this.productsData.items.map(product => {
                return {
                    ...product,
                    img: product.preview.file.url
                };
            }) : [];
        },
        items() {
            return this.productsData ? this.productsData.pagination.total : 0;
        },
        loadProductsParams() {
            const obj = {};
            obj.params = {};
            if (this.page) { obj.params.page = this.page; }
            if (this.productsPerPage) { obj.params.limit = this.productsPerPage; }
            if (this.filterCategoryId) { obj.params.categoryId = this.filterCategoryId; }
            if (this.filterPriceFrom) { obj.params.minPrice = this.filterPriceFrom; }
            if (this.filterPriceTo) { obj.params.maxPrice = this.filterPriceTo; }
            if (this.filterProductsProps) { obj.params.props = this.filterProductsProps; }
            return obj;
        }
    },
    watch: {
        page() {
            this.loadProducts();
        },
        filterCategoryId() {
            this.loadProducts();
        },
        filterPriceFrom() {
            this.loadProducts();
        },
        filterPriceTo() {
            this.loadProducts();
        },
        filterColorsId() {
            this.loadProducts();
        },
        filtering(value) {
            if (value) {
                this.loadProducts();
            }
        },
        $route(to, from) {
            this.filterCategoryId = this.$route.params.id || 0;
        }
    },
    methods: {
        containsSome,
        loadProducts() {
            this.productsLoading = true;
            this.productsLoadingFailed = false;
            clearTimeout(this.loadProductsTimer);
            this.loadProductsTimer = setTimeout(() => {
                axios.get(`${API_BASE_URL}/api/products`, this.loadProductsParams)
                    .then(res => {
                        if (this.filterColorsId.length) {
                            res.data.items = res.data.items.filter(item => {
                                return this.containsSome(item.colors.map(color => color.id), this.filterColorsId);
                            });
                        }
                        this.productsData = res.data;
                        this.filtering = false;
                    })
                    .catch(() => {
                        this.productsLoadingFailed = true;
                        this.filtering = false;
                    })
                    .then(() => {
                        this.productsLoading = false;
                        this.filtering = false;
                    });
            }, 0);
        },
    },
    created() {
        this.filterCategoryId = this.$route.params.id || 0;
        this.loadProducts();
    },
};
</script>
