<template>
    <Preloader v-if="filtersLoading" />
    <Fail v-else-if="filtersLoadingFailed" v-bind:callback="loadFilters">    ...</Fail>
    <aside v-else class="filter">
        <h2 class="filter__title"></h2>
        <form class="filter__form form" v-on:submit.prevent="submitHandler">
            <fieldset class="form__block">
                <legend class="form__legend"></legend>
                <label class="form__label form__label--price">
                    <input class="form__input" type="text" v-model.number="currentPriceFrom">
                    <span class="form__value"></span>
                </label>
                <label class="form__label form__label--price">
                    <input class="form__input" type="text" v-model.number="currentPriceTo">
                    <span class="form__value"></span>
                </label>
            </fieldset>

            <fieldset class="form__block">
                <legend class="form__legend"></legend>
                <label class="form__label form__label--select">
                    <select class="form__select" type="text" v-model.number="currentCategoryId" v-on:change="loadFilters">
                        <option value="0"> </option>
                        <option v-for="category in categories" v-bind:value="category.id" v-bind:key="category.id">
                            {{ category.title }}
                        </option>
                    </select>
                </label>
            </fieldset>
            <fieldset class="form__block" v-if="productPropsData">
                <legend class="form__legend"></legend>
                <ul class="colors">
                    <li class="colors__item" v-for="color of colors">
                        <label class="colors__label">
                            <input class="colors__radio sr-only" type="checkbox" name="color" v-bind:value="color.id"
                                v-model="currentColorsId" v-bind:key="color.id">
                            <span class="colors__value" v-bind:style="'background-color: ' + color.code">
                            </span>
                        </label>
                    </li>
                </ul>
            </fieldset>
            <fieldset class="form__block" v-for="prop in productPropsData" v-if="productPropsData && prop.code !== 'color'"
                v-bind:key="prop.id">
                <legend class="form__legend">{{ prop.title }}</legend>
                <ul class="check-list">
                    <li class="check-list__item" v-for="item in prop.availableValues" v-bind:key="item.id">
                        <label class="check-list__label">
                            <input class="check-list__check sr-only" type="checkbox" v-bind:name="prop.code"
                                v-bind:value="item.value" v-model="currentProductsProps[prop.code]">
                            <span class="check-list__desc">
                                {{ item.value }}
                            </span>
                        </label>
                    </li>
                </ul>
            </fieldset>
            <button class="filter__submit button button--primery" type="submit">
                
            </button>
            <button v-if="resetIsVisible" class="filter__reset button button--second" type="button"
                v-on:click.prevent="resetHandler">
                
            </button>
        </form>
    </aside>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';
export default {
    props: [
        'priceFrom',
        'priceTo',
        'categoryId',
        'colorsId',
        'productsProps',
        'filtering'
    ],
    data() {
        return {
            currentPriceFrom: '',
            currentPriceTo: '',
            currentCategoryId: 0,
            currentColorsId: [],
            currentProductsProps: {},
            categoriesData: null,
            colorsData: null,
            productPropsData: null,
            currentlyFiltering: false,
            filtersLoading: false,
            filtersLoadingFailed: false
        };
    },
    watch: {
        priceFrom(value) {
            this.currentPriceFrom = value;
        },
        priceTo(value) {
            this.currentPriceTo = value;
        },
        categoryId(value) {
            this.currentCategoryId = value;
            this.loadFilters();
        },
        colorsId(value) {
            this.currentColorsId = value;
        },
        filtering(value) {
            this.currentlyFiltering = value;
        }
    },
    methods: {
        submitHandler() {
            this.currentlyFiltering = true;
            this.updateHandlers();
        },
        resetHandler() {
            this.currentPriceFrom = '';
            this.currentPriceTo = '';
            this.currentCategoryId = 0;
            this.currentColorsId = [];
            this.currentProductsProps = [];
            this.currentlyFiltering = false;
            this.updateHandlers();
        },
        updateHandlers() {
            this.$emit('update:priceFrom', this.currentPriceFrom);
            this.$emit('update:priceTo', this.currentPriceTo);
            this.$emit('update:categoryId', this.currentCategoryId);
            this.$emit('update:colorsId', this.currentColorsId);
            this.$emit('update:productsProps', this.currentProductsProps);
            this.$emit('update:filtering', this.currentlyFiltering);
        },
        loadFilters() {
            const promises = [];

            this.filtersLoading = true;
            this.filtersLoadingFailed = false;

            promises
                .push(
                    axios
                        .get(`${API_BASE_URL}/api/productCategories`)
                        .then(res => this.categoriesData = res.data)
                );
            promises
                .push(
                    axios
                        .get(`${API_BASE_URL}/api/colors`)
                        .then(res => this.colorsData = res.data)
                );
            if (this.currentCategoryId) {
                promises
                    .push(
                        axios
                            .get(`${API_BASE_URL}/api/productCategories/${this.currentCategoryId}`)
                            .then(res => {
                                this.productPropsData = res.data.productProps;
                                this.currentProductsProps = {};
                                this.productPropsData.forEach(prop => {
                                    this.currentProductsProps[prop.code] = [];
                                });
                            })
                    );
            } else {
                this.productPropsData = null;
            }
            Promise.all(promises)
                .then(() => {
                    this.filtersLoading = false;
                })
                .catch(() => {
                    this.filtersLoading = false;
                    this.filtersLoadingFailed = true;
                });
        }
    },
    created() {
        this.currentCategoryId = this.categoryId;
        this.loadFilters();
    },
    computed: {
        categories() {
            return this.categoriesData ? this.categoriesData.items : [];
        },
        colors() {
            return this.colorsData ? this.colorsData.items : [];
        },
        resetIsVisible() {
            if (this.currentCategoryId || this.currentPriceFrom || this.currentPriceTo) {
                return true;
            }
            return false;
        }
    },

};
</script>
