<template>
    <div>
        <Preloader v-if="productLoading" />
        <Fail v-else-if="productLoadingFailed" v-bind:callback="loadProduct">    ...</Fail>
        <main class="content container" v-else>
            <div class="content__top">
                <ul class="breadcrumbs">
                    <li class="breadcrumbs__item">
                        <router-link class="breadcrumbs__link" v-bind:to="{ name: 'main' }">
                            
                        </router-link>
                    </li>
                    <li class="breadcrumbs__item">
                        <router-link class="breadcrumbs__link"
                            v-bind:to="{ name: 'category', params: { id: category.id } }">
                            {{ category.title }}
                        </router-link>
                    </li>
                    <li class="breadcrumbs__item">
                        <a class="breadcrumbs__link">
                            {{ product.title }}
                        </a>
                    </li>
                </ul>
            </div>

            <section class="item">
                <div class="item__pic">
                    <img v-bind:src="product.preview.file.url" v-bind:alt="offerTitle">
                </div>

                <div class="item__info">
                    <span class="item__code">: {{ offerId }}</span>
                    <h2 class="item__title">
                        {{ offerTitle }}
                    </h2>
                    <div class="item__form">
                        <form class="form" action="#" method="POST" v-on:submit.prevent="addToCart">
                            <b class="item__price">
                                {{ offerPrice | numberFormat }} 
                            </b>

                            <fieldset class="form__block">
                                <legend class="form__legend">:</legend>
                                <ul class="colors">
                                    <li class="colors__item" v-for="color in product.colors">
                                        <label class="colors__label">
                                            <input class="colors__radio sr-only" type="radio" name="color-item"
                                                v-bind:value="color" v-model="currentColor">
                                            <span class="colors__value"
                                                v-bind:style="'background-color: ' + color.color.code">
                                            </span>
                                        </label>
                                    </li>
                                </ul>
                            </fieldset>
                            <fieldset class="form__block" v-if="product.mainProp.code !== 'color'">
                                <legend class="form__legend">{{ product.mainProp.title }}:</legend>

                                <ul class="sizes sizes--primery">
                                    <li class="sizes__item" v-for="offer in product.offers">
                                        <label class="sizes__label" v-bind:title="product.mainProp.title">
                                            <input class="sizes__radio sr-only" type="radio"
                                                v-bind:value="offer.propValues[0].value" v-model="checkedProp"
                                                v-bind:key="offer.id">
                                            <span class="sizes__value">
                                                {{ offer.propValues[0].value }}
                                            </span>
                                        </label>
                                    </li>
                                </ul>
                            </fieldset>
                            <div class="item__row">
                                <ProductCounter v-bind:amount.sync="productAmount" />
                                <button class="button button--primery" type="submit" v-bind:disabled="productAddSending">
                                     
                                </button>
                            </div>
                            <div v-show="productAdded">   </div>
                            <div v-show="productAddSending">   ...</div>
                        </form>
                    </div>
                </div>

                <div class="item__desc">
                    <ul class="tabs">
                        <li class="tabs__item" v-for="tab in tabs.list">
                            <a class="tabs__link" v-bind:href="tabs.current !== tab.name"
                                v-bind:class="{ 'tabs__link--current': tabs.current === tab.name }"
                                v-on:click.prevent="tabs.current = tab.name">
                                {{ tab.name }}
                            </a>
                        </li>
                    </ul>

                    <div class="item__content" v-html="tabsContent">
                    </div>
                </div>
            </section>
        </main>
    </div>
</template>
<script>
import numberFormat from '@/helpers/numberFormat';
import ProductCounter from '@/components/ProductCounter';
import { API_BASE_URL } from '@/config';
import axios from 'axios';
import { mapActions } from 'vuex';

export default {
    data() {
        return {
            productAmount: 1,
            productData: null,
            productLoading: false,
            productLoadingFailed: false,
            productAdded: false,
            productAddSending: false,
            currentColor: null,
            checkedProp: null,
            tabs: {
                list: [
                    {
                        name: '',
                    },
                    {
                        name: '',
                    },
                ],
                current: '',
            },
        };
    },
    computed: {
        product() {
            return this.productData;
        },
        category() {
            return this.productData.category;
        },
        offerPrice() {
            if (this.product.mainProp.code === 'color') {
                const what = this.currentColor.color.title.split('').join('');
                const offer = this.product.offers.filter(offer => offer.propValues[0].value == what);
                if (offer.length) {
                    return offer[0].price;
                } else {
                    return this.product.offers[0].price;
                }
            } else {
                return this.product.offers
                    .find(offer => offer.propValues[0].value == this.checkedProp)
                    .price;
            }
        },
        offerTitle() {
            if (this.product.mainProp.code === 'color') {
                return `${this.product.title} (${this.currentColor.color.title})`;
            } else {
                return this.product.offers
                    .find(offer => offer.propValues[0].value == this.checkedProp)
                    .title + ` (${this.currentColor.color.title})`;
            }
        },
        offerId() {
            return this.product.offers
                .find(offer => offer.propValues[0].value == this.checkedProp)
                .id;
        },
        tabsContent() {
            if (this.tabs.current === '') {
                return this.product.title || ' ...';
            } else {
                if (this.product) {
                    let specEl = '<div class="item__spec">';
                    this.product.specifications.forEach(spec => {
                        specEl += `<b>${spec.title}:</b>`;
                        specEl += `<span>${spec.value}</span>`;
                    });
                    specEl += '</div>';
                    return specEl;
                } else {
                    return ' ...';
                }
            }
        }
    },
    filters: {
        numberFormat
    },
    components: {
        ProductCounter
    },
    methods: {
        ...mapActions(['addProductToCart']),
        addToCart() {
            this.productAdded = false;
            this.productAddSending = true;
            let productOfferId = this.product.offers.find(offer => offer.propValues[0].value == this.checkedProp).id;
            this.addProductToCart({ productOfferId, colorId: this.currentColor.color.id, quantity: this.productAmount })
                .then(() => {
                this.productAdded = true;
                this.productAddSending = false;
            });
        },
        loadProduct() {
            this.productLoading = true;
            this.productLoadingFailed = false;

            clearTimeout(this.loadProductTimer);

            this.loadProductTimer = setTimeout(() => {
                axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
                    .then(res => {
                        this.productData = res.data;
                        this.currentColor = this.product.colors.find(element => element.color.id == this.$route.params.color);
                        this.checkedProp = this.$route.params.offer;
                    })
                    .catch(() => this.productLoadingFailed = true)
                    .then(() => this.productLoading = false);
            }, 0);
        },
    },
    watch: {
        '$route.params.id': {
            handler() {
                this.loadProduct();
            },
            immediate: true
        },
        '$route.params.color': {
            handler() {
                this.loadProduct();
            },
            immediate: true
        },
        '$route.params.offer': {
            handler() {
                this.loadProduct();
            },
            immediate: true
        },
    }
};
</script>
