import numberFormat from '@/helpers/numberFormat';
import inputNumber from '@/helpers/inputNumbers';
import { mapActions } from 'vuex';
export default {
    props: [
        'item'
    ],
    methods: {
        inputNumber,
        ...mapActions(['updateCartProductAmount', 'deleteCartProduct']),
    },
    computed: {
        amount: {
            get() {
                return this.item.quantity;
            },
            set(value) {
                this.updateCartProductAmount({ productId: this.item.id, amount: value });
            }
        },
        cartItemTitle() {
            if (this.item.productOffer.product.mainProp.code === 'color') {
                return `${this.item.productOffer.product.title} (${this.item.color.color.title})`;
            } else {
                return this.item.productOffer.title;
            }
        }
    },
    filters: {
        numberFormat
    },

};
