<template>
    <li class="catalog__item">
        <router-link class="catalog__pic" v-bind:to="{
            name: 'product', params: {
                id: product.id,
                color: currentColor.color.id,
                offer: checkedProp
            }
        }" v-bind:title="product.title">
            <img v-bind:src="product.img" v-bind:alt="product.title">
        </router-link>

        <h3 class="catalog__title">
            <router-link v-bind:to="{
                name: 'product', params: {
                    id: product.id,
                    color: currentColor.color.id,
                    offer: checkedProp
                }
            }">
                {{ offerTitle }}
            </router-link>
        </h3>

        <span class="catalog__price">
            {{ offerPrice | numberFormat }} .
        </span>
        <div class="catalog__prop-wrap">
            <ul class="sizes" v-if="product.mainProp.code !== 'color'">
                <li class="sizes__item" v-for="offer in product.offers">
                    <label class="sizes__label" v-bind:title="product.mainProp.title">
                        <input class="sizes__radio sr-only" type="radio" v-bind:value="offer.propValues[0].value"
                            v-model="checkedProp" v-bind:key="offer.id">
                        <span class="sizes__value">
                            {{ offer.propValues[0].value }}
                        </span>
                    </label>
                </li>
            </ul>
        </div>
        <div class="catalog__colors-wrap">
            <ul class="colors colors--black">
                <li class="colors__item" v-for="color in product.colors">
                    <label class="colors__label" v-bind:title="color.color.title">
                        <input class="colors__radio sr-only" type="radio" v-bind:value="color" v-model="currentColor"
                            v-bind:key="color.id">
                        <span class="colors__value" v-bind:style="'background-color: ' + color.color.code">
                        </span>
                    </label>
                </li>
            </ul>
        </div>
    </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

export default {
    props: ['product'],
    data() {
        return {
            currentColor: this.product.colors[0],
            checkedProp: this.product.offers[0].propValues[0].value
        };
    },
    filters: {
        numberFormat
    },
    computed: {
        offerPrice() {
            if (this.product.mainProp.code === 'color') {
                const what = this.currentColor.color.title.split('').join('');
                const offer = this.product.offers.find(offer => offer.propValues[0].value == what);
                if (offer) {
                    return offer.price;
                } else {
                    return this.product.offers[0].price;
                }
            } else {
                return this.product.offers.find(offer => offer.propValues[0].value == this.checkedProp).price;
            }
        },
        offerTitle() {
            if (this.product.mainProp.code === 'color') {
                return `${this.product.title} (${this.currentColor.color.title})`;
            } else {
                return this.product.offers.find(offer => {
                    return offer.propValues[0].value == this.checkedProp;
                }).title + ` (${this.currentColor.color.title})`;
            }
        },

    }
};
</script>
