import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        cartProducts: [],
        userAccessKey: null,
        cartProductsData: [],
        cartLoading: false,
        cartLoadingFailed: false,
        deliveriesData: null,
        paymentsData: null,
        orderInfo: null,
    },
    mutations: {
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo;
        },
        resetCart(state) {
            state.cartProducts = [];
            state.cartProductsData = [];
        },
        updateCartProductAmount(state, { productId, amount }) {
            const item = this.state.cartProducts.find(item => item.productId === productId);
            if (item) {
                item.amount = amount;
            }
        },
        updateUserAccessKey(state, accessKey) {
            state.userAccessKey = accessKey;
        },
        updateCartProductsData(state, items) {
            state.cartProductsData = items;
        },
        updateCartLoading(state, currentState) {
            state.cartLoading = currentState;
        },
        updateCartLoadingFailed(state, currentState) {
            state.cartLoadingFailed = currentState;
        },
        updateDeliveriesData(state, items) {
            state.deliveriesData = items;
        },
        updatePaymentsData(state, items) {
            state.paymentsData = items;
        },
        syncCartProducts(state) {
            state.cartProducts = state.cartProductsData.map(item => {
                return {
                    productId: item.productOffer.id,
                    amount: item.quantity
                };
            });
        }
    },
    getters: {
        cartDetailProducts(state) {
            return state.cartProductsData;
        },
        cartLoadingState(state) {
            return {
                loading: state.cartLoading,
                failed: state.cartLoadingFailed,
            }
        },
        cartTotalPrice(state, getters) {
            return getters.cartDetailProducts.reduce((acc, item) => {
                return acc + item.price * item.quantity;
            }, 0);
        },
        cartProductsAmount(state) {
            return state.cartProducts.length;
        },
        cartProductsTotalAmount(state) {
            if (state.cartProducts.length) {
                return state.cartProducts.reduce((acc, product) => acc += product.amount, 0);
            } else {
                return '';
            }
        },
        deliveries(state) {
            return state.deliveriesData;
        },
        payments(state) {
            return state.paymentsData;
        },
        orderDetails(state) {
            return state.orderInfo;
        }
    },
    actions: {
        loadOrderInfo(context, orderId) {
            return axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            }).then(res => {
                context.commit('updateOrderInfo', res.data);
            });
        },
        loadCart(context) {
            context.commit('updateCartLoading', true);
            context.commit('updateCartLoadingFailed', false);
            return axios.get(`${API_BASE_URL}/api/baskets`, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            }).then(res => {
                if (!context.state.userAccessKey) {
                    localStorage.setItem('userAccessKey', res.data.user.accessKey);
                    context.commit('updateUserAccessKey', res.data.user.accessKey);
                }
                context.commit('updateCartProductsData', res.data.items);
                context.commit('syncCartProducts', res.data.items);
                context.commit('updateCartLoading', false);
            }).catch(() => {
                context.commit('updateCartLoading', false);
                context.commit('updateCartLoadingFailed', true);
            });
        },
        loadDeliveries(context) {
            return axios.get(`${API_BASE_URL}/api/deliveries`)
                .then(res => context.commit('updateDeliveriesData', res.data));
        },
        loadPayments(context, deliveryTypeId) {
            return axios.get(`${API_BASE_URL}/api/payments`, {
                params: {
                    deliveryTypeId
                }
            }).then(res => context.commit('updatePaymentsData', res.data.reverse()));
        },
        addProductToCart(context, { productOfferId, colorId, quantity }) {
            return axios.post(`${API_BASE_URL}/api/baskets/products`, {
                productOfferId,
                colorId,
                quantity,
            }, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            }).then(res => {
                context.commit('updateCartProductsData', res.data.items);
                context.commit('syncCartProducts', res.data.items);
            });
        },
        updateCartProductAmount(context, { productId, amount }) {
            context.commit('updateCartProductAmount', { productId, amount });
            if (amount < 1) {
                return;
            }
            return axios.put(`${API_BASE_URL}/api/baskets/products`, {
                basketItemId: productId,
                quantity: amount
            }, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            }).then(res => {
                context.commit('updateCartProductsData', res.data.items);
            }).catch(() => {
                context.commit('syncCartProducts', res.data.items);
            });
        },
        deleteCartProduct(context, basketItemId) {
            return axios.delete(`${API_BASE_URL}/api/baskets/products`, {
                data: {
                    basketItemId
                },
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            }).then(res => {
                context.commit('updateCartProductsData', res.data.items);
                context.commit('syncCartProducts', res.data.items);
            });
        }
    }
});
