<template>
    <main class="content container">
        <div class="content__top">
            <ul class="breadcrumbs">
                <li class="breadcrumbs__item">
                    <router-link class="breadcrumbs__link" v-bind:to="{ name: 'main' }">
                        
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <router-link class="breadcrumbs__link" v-bind:to="{ name: 'cart' }">
                        
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link">
                         
                    </a>
                </li>
            </ul>

            <h1 class="content__title">
                
            </h1>
            <span class="content__info">
                : {{ cartProductsAmount }}
            </span>
        </div>

        <section class="cart">
            <form class="cart__form form" action="#" method="POST" v-on:submit.prevent="order">
                <div class="cart__field">
                    <div class="cart__data">
                        <BaseFormText title="" placeholder="   " v-model="formData.name"
                            v-bind:error="formError.name" />
                        <BaseFormText title=" " placeholder="  " v-model="formData.address"
                            v-bind:error="formError.address" />
                        <BaseFormText title="" placeholder="  " v-model="formData.phone" type="tel"
                            v-bind:error="formError.phone" />
                        <BaseFormText title="Email" placeholder="  Email" v-model="formData.email" type="email"
                            v-bind:error="formError.email" />
                        <BaseFormTextarea title="  " placeholder=" "
                            v-model="formData.comment" v-bind:error="formError.comment" />
                    </div>
                    <div class="cart__options">
                        <h3 class="cart__title"></h3>
                        <Preloader v-if="deliveriesLoading" />
                        <Fail v-else-if="deliveriesLoadingFailed" v-bind:callback="loadDeliveries">  
                             ...</Fail>
                        <ul v-else class="cart__options options">
                            <li class="options__item" v-for="item of deliveries" v-bind:key="item.id">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="delivery"
                                        v-bind:value="item.id" v-model="formData.deliveryTypeId">
                                    <span class="options__value">
                                        {{ item.title }} <b>{{ item.price > 0 ? item.price : '' }}</b>
                                    </span>
                                </label>
                            </li>
                        </ul>

                        <h3 class="cart__title"></h3>
                        <Preloader v-if="paymentsLoading" />
                        <Fail v-else-if="paymentsLoadingFailed" v-bind:callback="loadPayments">  
                             ...</Fail>
                        <ul v-else class="cart__options options">
                            <li class="options__item" v-for="item of payments">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="pay" v-bind:value="item.id"
                                        v-bind:key="item.id" v-model="formData.paymentTypeId">
                                    <span class="options__value">
                                        {{ item.title }}
                                    </span>
                                </label>
                            </li>
                        </ul>
                    </div>
                </div>
                <Preloader v-if="productsLoadingState.loading" />
                <Fail v-else-if="productsLoadingState.failed" v-bind:callback="loadCart">  
                     ...</Fail>
                <div v-else class="cart__block">
                    <ul class="cart__orders">
                        <CartOrder v-for="item in products" v-bind:key="item.id" v-bind:item="item" />
                    </ul>

                    <div class="cart__total">
                        <p><b>:</b> <br>
                            : <b>{{ cartProductsTotalAmount }}</b> <br>
                            &nbsp; <b>{{ totalPrice | numberFormat }}&nbsp;</b></p>
                    </div>

                    <button class="cart__button button button--primery" type="submit">
                        <svg v-if="formPosting" x="0px" y="0px" width="30" height="30" viewBox="0 0 48 48">
                            <g width="48" height="48">
                                <animateTransform attributeType="xml" attributeName="transform" type="rotate" from="0 24 24"
                                    to="360 24 24" dur="1.5s" repeatCount="indefinite" />
                                <path fill="#000000"
                                    d="M24,45C12.4,45,3,35.6,3,24S12.4,3,24,3V0l0,0C10.7,0,0,10.7,0,24c0,13.3,10.7,24,24,24V45z" />
                                <path fill="#9eff00"
                                    d="M24,3c11.6,0,21,9.4,21,21s-9.4,21-21,21v3l0,0c13.3,0,24-10.7,24-24C48,10.7,37.3,0,24,0V3z" />
                            </g>
                        </svg>
                        <span v-else> </span>
                    </button>
                </div>
                <div class="cart__error form__error-block" v-if="formErrorMessage">
                    <h4>  !</h4>
                    <p>
                        {{ formErrorMessage }}
                    </p>
                </div>
            </form>
        </section>
    </main>
</template>
<script>
import BaseFormText from '@/components/BaseFormText';
import BaseFormTextarea from '@/components/BaseFormTextarea';
import numberFormat from '@/helpers/numberFormat';
import CartOrder from '@/components/CartOrder';
import { mapGetters, mapMutations, mapActions } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
    components: {
        BaseFormText,
        BaseFormTextarea,
        CartOrder
    },
    data() {
        return {
            formData: {
                deliveryTypeId: 1,
                paymentTypeId: 2
            },
            formError: {},
            formErrorMessage: '',
            formPosting: false,
            deliveryTypeId: null,
            deliveriesLoading: false,
            deliveriesLoadingFailed: false,
            paymentsLoading: false,
            paymentsLoadingFailed: false,
        };
    },
    computed: {
        ...mapGetters({
            products: 'cartDetailProducts',
            productsLoadingState: 'cartLoadingState',
            totalPrice: 'cartTotalPrice',
            cartProductsAmount: 'cartProductsAmount',
            cartProductsTotalAmount: 'cartProductsTotalAmount',
            deliveries: 'deliveries',
            payments: 'payments'
        }),
    },
    filters: {
        numberFormat
    },
    methods: {
        ...mapActions(['loadDeliveries', 'loadPayments', 'loadCart']),
        ...mapMutations(['resetCart', 'updateOrderInfo']),
        order() {
            this.formError = {};
            this.formErrorMessage = '';
            this.formPosting = true;
            axios.post(`${API_BASE_URL}/api/orders`, {
                ...this.formData,

            }, {
                params: {
                    userAccessKey: this.$store.state.userAccessKey
                }
            }).then(res => {
                this.resetCart();
                this.updateOrderInfo(res.data);
                this.$router.push({ name: 'orderInfo', params: { id: res.data.id } });
                this.formPosting = false;

            }).catch(error => {
                this.formPosting = false;
                this.formErrorMessage = error.response.data.error.message || '';
                this.formError = error.response.data.error.request || {};
            });
        }
    },
    watch: {
        'formData.deliveryTypeId': function (val) {
            this.loadPayments(val).then(() => {
                if (this.payments.length === 1) {
                    this.formData.paymentTypeId = this.payments[0].id;
                }
            });
        }
    },
    created() {
        this.deliveriesLoading = true;
        this.deliveriesLoadingFailed = false;
        this.paymentsLoading = true;
        this.paymentsLoadingFailed = false;
        this.loadDeliveries()
            .then(() => {
                this.deliveriesLoading = false;
            })
            .catch(() => {
                this.deliveriesLoading = false;
                this.deliveriesLoading = true;
            });
        this.loadPayments(this.formData.deliveryTypeId)
            .then(() => {
                this.paymentsLoading = false;
            })
            .catch(() => {
                this.paymentsLoading = false;
                this.paymentsLoading = true;
            });
    }
};
</script>