<template>
    <main v-if="infoLoading" class="content container">
        <Preloader />
    </main>
    <main v-else-if="infoLoadingFailed" class="content container">
        <Fail v-bind:callback="loadOrderInfo">
                ...
        </Fail>
    </main>
    <main v-else class="content container">
        <div class="content__top">
            <ul class="breadcrumbs">
                <li class="breadcrumbs__item">
                    <router-link class="breadcrumbs__link" v-bind:to="{ name: 'main' }">
                        
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <router-link class="breadcrumbs__link" v-bind:to="{ name: 'cart' }">
                        
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link">
                          
                    </a>
                </li>
            </ul>
            <h1 class="content__title">
                  <span>{{ orderDetails.id }}</span>
            </h1>
        </div>

        <section class="cart">
            <form class="cart__form form" action="#" method="POST">
                <div class="cart__field">
                    <p class="cart__message">
                         &nbsp;  . &nbsp;    &nbsp;
                        .
                           &nbsp; &nbsp;     .
                    </p>

                    <ul class="dictionary">
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                
                            </span>
                            <span class="dictionary__value">
                                {{ orderDetails.name }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                 
                            </span>
                            <span class="dictionary__value">
                                {{ orderDetails.address }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                
                            </span>
                            <span class="dictionary__value">
                                {{ orderDetails.phone }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                Email
                            </span>
                            <span class="dictionary__value">
                                {{ orderDetails.email }}
                            </span>
                        </li>
                        <li class="dictionary__item">
                            <span class="dictionary__key">
                                 
                            </span>
                            <span class="dictionary__value">
                                {{ orderDetails.paymentType }}
                            </span>
                        </li>
                    </ul>
                </div>

                <div class="cart__block">
                    <ul class="cart__orders">
                        <CartOrder v-for="item in orderDetails.basket.items" v-bind:key="item.id" v-bind:item="item" />
                        <!--<li v-for="item in orderDetails.basket.items" class="cart__order"
                                v-bind:key="item.productOffer.id">
                                <h3>{{ item.productOffer.title }}</h3>
                                <b>{{ item.price * item.quantity | numberFormat }} </b>
                                <span>: {{ item.productOffer.id }}</span>
                                <span>{{ item.quantity }} .</span>
                            </li>-->
                    </ul>

                    <div class="cart__total">
                        <p>: <b>{{ orderDetails.deliveryType.price | numberFormat }}&nbsp;</b></p>
                        <p>:
                             <b>{{ totalItemsInBasket }}</b>
                            &nbsp; <b>{{ priceWithDelivery | numberFormat }}&nbsp;</b>
                        </p>
                    </div>
                </div>
            </form>
        </section>
    </main>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import CartOrder from '@/components/CartOrder';
export default {
    data() {
        return {
            infoLoading: false,
            infoLoadingFailed: false,
        };
    },
    components: {
        CartOrder
    },
    computed: {
        ...mapGetters({ orderDetails: 'orderDetails' }),
        totalItemsInBasket() {
            return this.orderDetails.basket.items.reduce((acc, item) => acc += item.quantity, 0);
        },
        priceWithDelivery() {
            return Number(this.orderDetails.deliveryType.price) + Number(this.orderDetails.totalPrice);
        }

    },
    methods: {
        ...mapActions(['loadOrderInfo'])
    },
    created() {
        this.infoLoading = true;
        this.infoLoadingFailed = false;
        if (this.orderDetails && this.orderDetails.id === this.$route.params.id) {
            this.infoLoading = false;
            return;
        }
        this.loadOrderInfo(this.$route.params.id)
            .catch(() => {
                this.infoLoadingFailed = true;
            })
            .then(() => {
                this.infoLoading = false;
            });
    },
    filters: {
        numberFormat
    }
};
</script>